.header{
    position: fixed;
    top: 0%;
    width: 100%;
    display: flex;
    z-index: 999;
    justify-content:space-between;
    background-color: black;

    /* animation */
     transition-timing-function: ease-in;
     transition: all 0.5s; 
  

   
}

.header__logo{
    position: fixed;
    object-fit: contain;
    width: 100px;
    left: 20px;
   
}

.header__userIcon{
    position: fixed;
    object-fit: contain;
    width: 30PX;
    right: 20px;

}

.header__black{
    background-color: #111;
    height: 30px;
}